import React from "react";
import ReactDOM from "react-dom";

import Tabs from "./Tabs";
import Panel from "./Panel";

import { events } from "./data";

import "./styles.css";

const myInfluencers = [33, 81];

const userId = 4;

function Tabsreact() {
  return (
    <Tabs>
      <Panel title="My Events">
        {events
          .filter(event => event.host_id !== userId)
          .map(event => {
            return <div key={event.id}>{event.title}</div>;
          })}
      </Panel>
      <Panel title="Influencer Events">
        {events
          .filter(event => myInfluencers.includes(event.host_id))
          .map(event => {
            return <div key={event.id}>{event.title}</div>;
          })}
      </Panel>
      <Panel title="All Events">
        {events.map(event => {
          return <div key={event.id}>{event.title}</div>;
        })}
      </Panel>
    </Tabs>
  );
}

export default Tabsreact;
